import React from "react";
import {LHS, RHS} from './general/style/common';
import {SeactionHeader, H2} from './general/style/style';
import { DhProperties } from "../DhProperties";

class Card extends React.Component {


  onSeeAllClick = () => {
    try {

      let { seeAllUrl, seeAllPageTitle = "" } = this.props;
      if (typeof (seeAllUrl) === "string") {
        seeAllUrl = seeAllUrl.replace("<lang>", (lang!="undefined")?lang:"en");
      }
      if(this.props.topicId && typeof(newsHuntAction)!=='undefined'){
        typeof(redirect)!="undefined" && redirect("openTopic", this.props.topicId, this.props.subTopicId?this.props.subTopicId:"",this.props.deeplink?this.props.deeplink:"");
      }else if(typeof(newsHuntAction)!='undefined' && typeof(callEvent)!="undefined" && ((!isIos() && checkMinAndroidAppVersion(NH_BROWSER_SUPPORTED_VERSION)) || (isIos() && checkMinAndroidAppVersion(NH_BROWSER_SUPPORTED_VERSION_IOS)))  && seeAllUrl && seeAllPageTitle){
          const url= DhProperties.properties.pwaBaseUrl+"/webItem?webModel="+encodeURIComponent(
            JSON.stringify({
              title:seeAllPageTitle,
              url: `${this.props.baseUrl? this.props.baseUrl: DhProperties.properties.baseUrl}/${seeAllUrl}`
            }));
          callEvent(url);
      } else if(seeAllUrl){
        window.location.href = `${this.props.baseUrl? this.props.baseUrl: DhProperties.properties.baseUrl}/${seeAllUrl}`;
      }
    }catch(err){
      console.log("Card" ,err);
    }
  }

  render() {
    return (
      <>
        { this.props.sectionTitle && <H2 padding="10px 16px" id={this.props.sectionId}>{this.props.sectionTitle}</H2>}
        { (this.props.title && !this.props.sectionTitle) && (
          <SeactionHeader>
          { <LHS id={this.props.titleId}>{this.props.title}</LHS> }
          {this.props.seeall && <RHS onClick={()=>this.onSeeAllClick()} ><a>{this.props.seeall}</a></RHS> }
          </SeactionHeader>)
         }
        {this.props.children}
      </>
    );
  }
}

export default Card;






